'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { 
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog'

interface ThemeCustomizerProps {
  isOpen: boolean
  onClose: () => void
}

interface CustomTheme {
  textColor: string
  backgroundColor: string
  accentColor: string
  borderColor: string
}

const defaultThemes = {
  light: {
    textColor: '#0f172a',
    backgroundColor: '#ffffff',
    accentColor: '#f1f5f9',
    borderColor: '#e2e8f0'
  },
  dark: {
    textColor: '#f8fafc',
    backgroundColor: '#0f172a',
    accentColor: '#1e293b',
    borderColor: '#334155'
  },
  sepia: {
    textColor: '#5d4e37',
    backgroundColor: '#f4f1e8',
    accentColor: '#ede4d3',
    borderColor: '#d4c4a8'
  },
  forest: {
    textColor: '#1a3a1a',
    backgroundColor: '#f0f8f0',
    accentColor: '#e8f5e8',
    borderColor: '#c8e6c8'
  },
  ocean: {
    textColor: '#1e3a5f',
    backgroundColor: '#f0f8ff',
    accentColor: '#e6f3ff',
    borderColor: '#b3d9ff'
  }
}

export default function ThemeCustomizer({ isOpen, onClose }: ThemeCustomizerProps) {
  const [customTheme, setCustomTheme] = useState<CustomTheme>(defaultThemes.light)
  const [selectedPreset, setSelectedPreset] = useState<string>('light')

  useEffect(() => {
    // Carregar tema personalizado do localStorage
    const savedTheme = localStorage.getItem('notesapp_custom_theme')
    if (savedTheme) {
      try {
        setCustomTheme(JSON.parse(savedTheme))
      } catch (error) {
        console.error('Erro ao carregar tema personalizado:', error)
      }
    }
  }, [])

  const applyTheme = (theme: CustomTheme) => {
    const root = document.documentElement
    
    // Aplicar variáveis CSS customizadas
    root.style.setProperty('--custom-text-color', theme.textColor)
    root.style.setProperty('--custom-bg-color', theme.backgroundColor)
    root.style.setProperty('--custom-accent-color', theme.accentColor)
    root.style.setProperty('--custom-border-color', theme.borderColor)
    
    // Salvar no localStorage
    localStorage.setItem('notesapp_custom_theme', JSON.stringify(theme))
    localStorage.setItem('notesapp_theme_enabled', 'true')
    
    setCustomTheme(theme)
  }

  const resetTheme = () => {
    const root = document.documentElement
    
    // Remover variáveis CSS customizadas
    root.style.removeProperty('--custom-text-color')
    root.style.removeProperty('--custom-bg-color')
    root.style.removeProperty('--custom-accent-color')
    root.style.removeProperty('--custom-border-color')
    
    // Remover do localStorage
    localStorage.removeItem('notesapp_custom_theme')
    localStorage.removeItem('notesapp_theme_enabled')
    
    setCustomTheme(defaultThemes.light)
    setSelectedPreset('light')
  }

  const handlePresetSelect = (presetName: string) => {
    const preset = defaultThemes[presetName as keyof typeof defaultThemes]
    if (preset) {
      setSelectedPreset(presetName)
      applyTheme(preset)
    }
  }

  const handleCustomColorChange = (property: keyof CustomTheme, value: string) => {
    const newTheme = { ...customTheme, [property]: value }
    setCustomTheme(newTheme)
    applyTheme(newTheme)
    setSelectedPreset('custom')
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>Personalizar Tema</DialogTitle>
          <DialogDescription>
            Customize as cores do texto e fundo da sua aplicação de anotações
          </DialogDescription>
        </DialogHeader>

        <Tabs defaultValue="presets" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="presets">Temas Predefinidos</TabsTrigger>
            <TabsTrigger value="custom">Personalizado</TabsTrigger>
          </TabsList>

          <TabsContent value="presets" className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {Object.entries(defaultThemes).map(([name, theme]) => (
                <Card 
                  key={name}
                  className={`cursor-pointer transition-all hover:shadow-md ${
                    selectedPreset === name ? 'ring-2 ring-primary' : ''
                  }`}
                  onClick={() => handlePresetSelect(name)}
                >
                  <CardHeader className="pb-2">
                    <CardTitle className="text-sm capitalize">{name}</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div 
                      className="h-16 rounded border-2 flex items-center justify-center text-sm font-medium"
                      style={{
                        backgroundColor: theme.backgroundColor,
                        color: theme.textColor,
                        borderColor: theme.borderColor
                      }}
                    >
                      Exemplo de texto
                    </div>
                    <div className="mt-2 flex gap-2">
                      <div 
                        className="w-4 h-4 rounded border"
                        style={{ backgroundColor: theme.textColor }}
                        title="Cor do texto"
                      />
                      <div 
                        className="w-4 h-4 rounded border"
                        style={{ backgroundColor: theme.backgroundColor }}
                        title="Cor de fundo"
                      />
                      <div 
                        className="w-4 h-4 rounded border"
                        style={{ backgroundColor: theme.accentColor }}
                        title="Cor de destaque"
                      />
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </TabsContent>

          <TabsContent value="custom" className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <Label htmlFor="textColor">Cor do Texto</Label>
                  <div className="flex gap-2 mt-1">
                    <Input
                      id="textColor"
                      type="color"
                      value={customTheme.textColor}
                      onChange={(e) => handleCustomColorChange('textColor', e.target.value)}
                      className="w-16 h-10 p-1 border rounded"
                    />
                    <Input
                      type="text"
                      value={customTheme.textColor}
                      onChange={(e) => handleCustomColorChange('textColor', e.target.value)}
                      placeholder="#000000"
                      className="flex-1"
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="backgroundColor">Cor de Fundo</Label>
                  <div className="flex gap-2 mt-1">
                    <Input
                      id="backgroundColor"
                      type="color"
                      value={customTheme.backgroundColor}
                      onChange={(e) => handleCustomColorChange('backgroundColor', e.target.value)}
                      className="w-16 h-10 p-1 border rounded"
                    />
                    <Input
                      type="text"
                      value={customTheme.backgroundColor}
                      onChange={(e) => handleCustomColorChange('backgroundColor', e.target.value)}
                      placeholder="#ffffff"
                      className="flex-1"
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="accentColor">Cor de Destaque</Label>
                  <div className="flex gap-2 mt-1">
                    <Input
                      id="accentColor"
                      type="color"
                      value={customTheme.accentColor}
                      onChange={(e) => handleCustomColorChange('accentColor', e.target.value)}
                      className="w-16 h-10 p-1 border rounded"
                    />
                    <Input
                      type="text"
                      value={customTheme.accentColor}
                      onChange={(e) => handleCustomColorChange('accentColor', e.target.value)}
                      placeholder="#f1f5f9"
                      className="flex-1"
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="borderColor">Cor das Bordas</Label>
                  <div className="flex gap-2 mt-1">
                    <Input
                      id="borderColor"
                      type="color"
                      value={customTheme.borderColor}
                      onChange={(e) => handleCustomColorChange('borderColor', e.target.value)}
                      className="w-16 h-10 p-1 border rounded"
                    />
                    <Input
                      type="text"
                      value={customTheme.borderColor}
                      onChange={(e) => handleCustomColorChange('borderColor', e.target.value)}
                      placeholder="#e2e8f0"
                      className="flex-1"
                    />
                  </div>
                </div>
              </div>

              <div>
                <Label>Pré-visualização</Label>
                <div 
                  className="mt-1 p-4 rounded-lg border-2 min-h-[200px]"
                  style={{
                    backgroundColor: customTheme.backgroundColor,
                    color: customTheme.textColor,
                    borderColor: customTheme.borderColor
                  }}
                >
                  <h3 className="text-lg font-semibold mb-2">Título da Anotação</h3>
                  <p className="text-sm opacity-75 mb-3">Subtítulo da anotação</p>
                  <p className="text-sm leading-relaxed">
                    Este é um exemplo de como o texto ficará com as cores personalizadas. 
                    Você pode ajustar as cores até encontrar a combinação perfeita para suas anotações.
                  </p>
                  <div 
                    className="mt-3 p-2 rounded"
                    style={{ backgroundColor: customTheme.accentColor }}
                  >
                    <p className="text-xs">Área de destaque</p>
                  </div>
                </div>
              </div>
            </div>
          </TabsContent>
        </Tabs>

        <div className="flex justify-between pt-4 border-t">
          <Button variant="outline" onClick={resetTheme}>
            Resetar Tema
          </Button>
          <Button onClick={onClose}>
            Fechar
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}
