export interface Note {
  id: string
  title: string
  subtitle: string
  content: string
  createdAt: string
  updatedAt: string
  folderId: string
}

export interface Folder {
  id: string
  name: string
  createdAt: string
  updatedAt: string
}

export interface NotesData {
  folders: Folder[]
  notes: Note[]
}

const STORAGE_KEY = 'notesapp_data'

export const saveData = (data: NotesData): void => {
  try {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(data))
  } catch (error) {
    console.error('Erro ao salvar dados:', error)
    throw new Error('Falha ao salvar dados')
  }
}

export const loadData = (): NotesData => {
  try {
    const stored = localStorage.getItem(STORAGE_KEY)
    if (!stored) {
      // Dados iniciais padrão
      const defaultData: NotesData = {
        folders: [
          {
            id: 'default-folder',
            name: 'Minhas Anotações',
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString()
          }
        ],
        notes: [
          {
            id: 'welcome-note',
            title: 'Bem-vindo ao NotesApp!',
            subtitle: 'Sua primeira anotação',
            content: 'Este é um exemplo de anotação. Você pode editar este texto, criar novas anotações e organizar tudo em pastas.\n\nRecursos disponíveis:\n• Criar e organizar pastas\n• Adicionar, editar e excluir anotações\n• Buscar por conteúdo\n• Modo escuro\n• Design responsivo',
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString(),
            folderId: 'default-folder'
          }
        ]
      }
      saveData(defaultData)
      return defaultData
    }
    return JSON.parse(stored)
  } catch (error) {
    console.error('Erro ao carregar dados:', error)
    // Retornar dados padrão em caso de erro
    return {
      folders: [],
      notes: []
    }
  }
}

export const deleteData = (): void => {
  try {
    localStorage.removeItem(STORAGE_KEY)
  } catch (error) {
    console.error('Erro ao deletar dados:', error)
    throw new Error('Falha ao deletar dados')
  }
}

// Funções auxiliares para gerar IDs únicos
export const generateId = (): string => {
  return Date.now().toString(36) + Math.random().toString(36).substr(2)
}

// Funções específicas para manipular pastas
export const createFolder = (name: string): Folder => {
  return {
    id: generateId(),
    name,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString()
  }
}

// Funções específicas para manipular notas
export const createNote = (folderId: string, title: string = 'Nova Anotação'): Note => {
  return {
    id: generateId(),
    title,
    subtitle: '',
    content: '',
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
    folderId
  }
}

export const updateNote = (note: Note, updates: Partial<Omit<Note, 'id' | 'createdAt'>>): Note => {
  return {
    ...note,
    ...updates,
    updatedAt: new Date().toISOString()
  }
}
