'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { useLocalStorage } from '@/hooks/useLocalStorage'
import Sidebar from '@/components/Sidebar'
import NotesList from '@/components/NotesList'
import EnhancedNoteEditorWithPopup from '@/components/EnhancedNoteEditorWithPopup'
import Header from '@/components/Header'

export default function DashboardPage() {
  const router = useRouter()
  const {
    data,
    isLoading,
    addFolder,
    updateFolder,
    deleteFolder,
    addNote,
    updateNote,
    deleteNote,
    searchNotes
  } = useLocalStorage()

  const [selectedFolderId, setSelectedFolderId] = useState<string | null>(null)
  const [selectedNoteId, setSelectedNoteId] = useState<string | null>(null)
  const [searchQuery, setSearchQuery] = useState('')
  const [userEmail, setUserEmail] = useState<string>('')
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

  // Verificar autenticação e carregar dados do usuário
  useEffect(() => {
    const isLoggedIn = localStorage.getItem('notesapp_logged_in')
    const email = localStorage.getItem('notesapp_user_email')

    if (isLoggedIn !== 'true') {
      router.push('/login')
      return
    }

    if (email) {
      setUserEmail(email)
    }
  }, [router])

  // Selecionar primeira pasta automaticamente
  useEffect(() => {
    if (!isLoading && data.folders.length > 0 && !selectedFolderId) {
      setSelectedFolderId(data.folders[0].id)
    }
  }, [data.folders, selectedFolderId, isLoading])

  // Limpar nota selecionada quando mudar de pasta
  useEffect(() => {
    setSelectedNoteId(null)
  }, [selectedFolderId])

  const handleLogout = () => {
    localStorage.removeItem('notesapp_logged_in')
    localStorage.removeItem('notesapp_user_email')
    router.push('/login')
  }

  const handleFolderSelect = (folderId: string) => {
    setSelectedFolderId(folderId)
    setSearchQuery('') // Limpar busca ao trocar de pasta
  }

  const handleNoteAdd = () => {
    if (!selectedFolderId) return
    
    const newNote = addNote(selectedFolderId)
    setSelectedNoteId(newNote.id)
  }

  const handleNoteSelect = (noteId: string) => {
    setSelectedNoteId(noteId)
  }

  const handleNoteDelete = (noteId: string) => {
    deleteNote(noteId)
    if (selectedNoteId === noteId) {
      setSelectedNoteId(null)
    }
  }

  const handleSearchChange = (query: string) => {
    setSearchQuery(query)
    setSelectedNoteId(null) // Limpar seleção ao buscar
  }

  // Obter notas para exibir (filtradas por pasta ou busca)
  const getNotesToDisplay = () => {
    if (searchQuery.trim()) {
      return searchNotes(searchQuery)
    } else if (selectedFolderId) {
      return data.notes.filter(note => note.folderId === selectedFolderId)
    }
    return []
  }

  const notesToDisplay = getNotesToDisplay()
  const selectedNote = selectedNoteId 
    ? data.notes.find(note => note.id === selectedNoteId) || null
    : null

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="text-center space-y-4">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto"></div>
          <p className="text-muted-foreground">Carregando suas anotações...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="h-screen flex flex-col bg-background">
      {/* Header */}
      <Header
        searchQuery={searchQuery}
        onSearchChange={handleSearchChange}
        userEmail={userEmail}
      />

      {/* Main Content */}
      <div className="flex-1 flex overflow-hidden">
        {/* Mobile Menu Overlay */}
        {isMobileMenuOpen && (
          <div 
            className="fixed inset-0 bg-black/50 z-40 lg:hidden"
            onClick={() => setIsMobileMenuOpen(false)}
          />
        )}

        {/* Sidebar */}
        <div className={`
          ${isMobileMenuOpen ? 'translate-x-0' : '-translate-x-full'}
          lg:translate-x-0 fixed lg:relative z-50 lg:z-auto
          transition-transform duration-300 ease-in-out
        `}>
          <Sidebar
            folders={data.folders}
            selectedFolderId={selectedFolderId}
            onFolderSelect={handleFolderSelect}
            onFolderAdd={addFolder}
            onFolderUpdate={updateFolder}
            onFolderDelete={deleteFolder}
            onLogout={handleLogout}
          />
        </div>

        {/* Notes List */}
        <div className="hidden md:block">
          <NotesList
            notes={notesToDisplay}
            selectedNoteId={selectedNoteId}
            onNoteSelect={handleNoteSelect}
            onNoteAdd={handleNoteAdd}
            onNoteDelete={handleNoteDelete}
            searchQuery={searchQuery}
          />
        </div>

        {/* Note Editor */}
        <div className="flex-1 flex flex-col">
          {/* Mobile: Show notes list or editor */}
          <div className="md:hidden">
            {selectedNoteId ? (
              <div className="flex flex-col h-full">
                {/* Mobile Header */}
                <div className="flex items-center justify-between p-4 border-b border-border">
                  <button
                    onClick={() => setSelectedNoteId(null)}
                    className="text-sm text-muted-foreground hover:text-foreground"
                  >
                    ← Voltar
                  </button>
                  <button
                    onClick={() => setIsMobileMenuOpen(true)}
                    className="text-sm text-muted-foreground hover:text-foreground"
                  >
                    Pastas
                  </button>
                </div>
            <EnhancedNoteEditorWithPopup
              note={selectedNote}
              onNoteUpdate={updateNote}
            />
              </div>
            ) : (
              <div className="flex flex-col h-full">
                {/* Mobile Header */}
                <div className="flex items-center justify-between p-4 border-b border-border">
                  <button
                    onClick={() => setIsMobileMenuOpen(true)}
                    className="text-sm text-muted-foreground hover:text-foreground"
                  >
                    ☰ Pastas
                  </button>
                  <button
                    onClick={handleNoteAdd}
                    className="text-sm bg-primary text-primary-foreground px-3 py-1 rounded"
                  >
                    + Nova
                  </button>
                </div>
                <NotesList
                  notes={notesToDisplay}
                  selectedNoteId={selectedNoteId}
                  onNoteSelect={handleNoteSelect}
                  onNoteAdd={handleNoteAdd}
                  onNoteDelete={handleNoteDelete}
                  searchQuery={searchQuery}
                />
              </div>
            )}
          </div>

          {/* Desktop: Always show editor */}
          <div className="hidden md:block flex-1">
            <EnhancedNoteEditorWithPopup
              note={selectedNote}
              onNoteUpdate={updateNote}
            />
          </div>
        </div>
      </div>
    </div>
  )
}
