'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { ScrollArea } from '@/components/ui/scroll-area'
import { Badge } from '@/components/ui/badge'
import { Note } from '@/lib/storage'

interface NotesListProps {
  notes: Note[]
  selectedNoteId: string | null
  onNoteSelect: (noteId: string) => void
  onNoteAdd: () => void
  onNoteDelete: (noteId: string) => void
  searchQuery?: string
}

export default function NotesList({
  notes,
  selectedNoteId,
  onNoteSelect,
  onNoteAdd,
  onNoteDelete,
  searchQuery = ''
}: NotesListProps) {
  const [draggedNoteId, setDraggedNoteId] = useState<string | null>(null)

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffInHours = (now.getTime() - date.getTime()) / (1000 * 60 * 60)

    if (diffInHours < 1) {
      return 'Agora há pouco'
    } else if (diffInHours < 24) {
      return `${Math.floor(diffInHours)}h atrás`
    } else if (diffInHours < 48) {
      return 'Ontem'
    } else {
      return date.toLocaleDateString('pt-BR', {
        day: '2-digit',
        month: '2-digit',
        year: '2-digit'
      })
    }
  }

  const truncateText = (text: string, maxLength: number = 100) => {
    if (text.length <= maxLength) return text
    return text.substring(0, maxLength) + '...'
  }

  const highlightSearchTerm = (text: string, query: string) => {
    if (!query.trim()) return text

    const regex = new RegExp(`(${query.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')})`, 'gi')
    const parts = text.split(regex)

    return parts.map((part, index) => 
      regex.test(part) ? (
        <mark key={index} className="bg-yellow-200 dark:bg-yellow-800 px-1 rounded">
          {part}
        </mark>
      ) : part
    )
  }

  const handleDragStart = (e: React.DragEvent, noteId: string) => {
    setDraggedNoteId(noteId)
    e.dataTransfer.effectAllowed = 'move'
  }

  const handleDragEnd = () => {
    setDraggedNoteId(null)
  }

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault()
    e.dataTransfer.dropEffect = 'move'
  }

  const handleDrop = (e: React.DragEvent, targetNoteId: string) => {
    e.preventDefault()
    if (draggedNoteId && draggedNoteId !== targetNoteId) {
      // Aqui você pode implementar a lógica de reordenação
      console.log(`Mover nota ${draggedNoteId} para posição da nota ${targetNoteId}`)
    }
    setDraggedNoteId(null)
  }

  return (
    <div className="w-80 bg-card border-r border-border h-full flex flex-col">
      {/* Header */}
      <div className="p-4 border-b border-border">
        <div className="flex items-center justify-between mb-2">
          <h3 className="text-lg font-semibold text-foreground">Anotações</h3>
          <Button
            variant="default"
            size="sm"
            onClick={onNoteAdd}
            className="h-8"
          >
            + Nova
          </Button>
        </div>
        
        {searchQuery && (
          <div className="flex items-center gap-2">
            <Badge variant="secondary" className="text-xs">
              {notes.length} resultado{notes.length !== 1 ? 's' : ''}
            </Badge>
            <span className="text-xs text-muted-foreground">
              para "{searchQuery}"
            </span>
          </div>
        )}
      </div>

      {/* Notes List */}
      <ScrollArea className="flex-1">
        <div className="p-2">
          {notes.length === 0 ? (
            <div className="text-center py-8">
              <p className="text-muted-foreground text-sm">
                {searchQuery ? 'Nenhuma anotação encontrada' : 'Nenhuma anotação ainda'}
              </p>
              {!searchQuery && (
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={onNoteAdd}
                  className="mt-2"
                >
                  Criar primeira anotação
                </Button>
              )}
            </div>
          ) : (
            <div className="space-y-2">
              {notes.map((note) => (
                <div
                  key={note.id}
                  draggable
                  onDragStart={(e) => handleDragStart(e, note.id)}
                  onDragEnd={handleDragEnd}
                  onDragOver={handleDragOver}
                  onDrop={(e) => handleDrop(e, note.id)}
                  className={`
                    group relative p-3 rounded-lg border cursor-pointer transition-all
                    ${selectedNoteId === note.id 
                      ? 'bg-accent border-accent-foreground/20' 
                      : 'bg-background hover:bg-accent/50 border-border'
                    }
                    ${draggedNoteId === note.id ? 'opacity-50' : ''}
                  `}
                  onClick={() => onNoteSelect(note.id)}
                >
                  {/* Note Content */}
                  <div className="space-y-2">
                    <div className="flex items-start justify-between">
                      <h4 className="font-medium text-sm text-foreground line-clamp-1">
                        {highlightSearchTerm(note.title || 'Sem título', searchQuery)}
                      </h4>
                      
                      {/* Delete button */}
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={(e) => {
                          e.stopPropagation()
                          onNoteDelete(note.id)
                        }}
                        className="opacity-0 group-hover:opacity-100 h-6 w-6 p-0 text-destructive hover:text-destructive"
                      >
                        ×
                      </Button>
                    </div>

                    {note.subtitle && (
                      <p className="text-xs text-muted-foreground line-clamp-1">
                        {highlightSearchTerm(note.subtitle, searchQuery)}
                      </p>
                    )}

                    {note.content && (
                      <p className="text-xs text-muted-foreground line-clamp-2">
                        {highlightSearchTerm(truncateText(note.content), searchQuery)}
                      </p>
                    )}

                    <div className="flex items-center justify-between">
                      <span className="text-xs text-muted-foreground">
                        {formatDate(note.updatedAt)}
                      </span>
                      
                      {draggedNoteId !== note.id && (
                        <div className="opacity-0 group-hover:opacity-100 text-xs text-muted-foreground">
                          ⋮⋮
                        </div>
                      )}
                    </div>
                  </div>

                  {/* Drag indicator */}
                  {draggedNoteId === note.id && (
                    <div className="absolute inset-0 border-2 border-dashed border-primary rounded-lg"></div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>
      </ScrollArea>
    </div>
  )
}
