'use client'

import { useState } from 'react'
import { useTheme } from 'next-themes'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Switch } from '@/components/ui/switch'
import { Label } from '@/components/ui/label'
import ThemeCustomizer from './ThemeCustomizer'

interface HeaderProps {
  searchQuery: string
  onSearchChange: (query: string) => void
  userEmail?: string
}

export default function Header({ searchQuery, onSearchChange, userEmail }: HeaderProps) {
  const { theme, setTheme } = useTheme()
  const [isSearchFocused, setIsSearchFocused] = useState(false)
  const [isThemeCustomizerOpen, setIsThemeCustomizerOpen] = useState(false)

  const toggleTheme = () => {
    setTheme(theme === 'dark' ? 'light' : 'dark')
  }

  return (
    <>
      <header className="h-16 bg-card border-b border-border px-6 flex items-center justify-between">
        {/* Search Bar */}
        <div className="flex-1 max-w-md">
          <div className={`relative transition-all duration-200 ${isSearchFocused ? 'scale-105' : ''}`}>
            <Input
              placeholder="Buscar anotações..."
              value={searchQuery}
              onChange={(e) => onSearchChange(e.target.value)}
              onFocus={() => setIsSearchFocused(true)}
              onBlur={() => setIsSearchFocused(false)}
              className="pl-10 pr-4 h-10 bg-background"
            />
            <div className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground">
              🔍
            </div>
            {searchQuery && (
              <Button
                variant="ghost"
                size="sm"
                onClick={() => onSearchChange('')}
                className="absolute right-2 top-1/2 transform -translate-y-1/2 h-6 w-6 p-0 hover:bg-muted"
              >
                ×
              </Button>
            )}
          </div>
        </div>

        {/* Right Section */}
        <div className="flex items-center gap-4">
          {/* Theme Customizer Button */}
          <Button
            variant="ghost"
            size="sm"
            onClick={() => setIsThemeCustomizerOpen(true)}
            className="text-sm text-muted-foreground hover:text-foreground"
            title="Personalizar cores"
          >
            🎨
          </Button>

          {/* Dark Mode Toggle */}
          <div className="flex items-center gap-2">
            <Label htmlFor="dark-mode" className="text-sm text-muted-foreground">
              {theme === 'dark' ? '🌙' : '☀️'}
            </Label>
            <Switch
              id="dark-mode"
              checked={theme === 'dark'}
              onCheckedChange={toggleTheme}
            />
          </div>

          {/* User Info */}
          {userEmail && (
            <div className="flex items-center gap-2 text-sm">
              <div className="w-8 h-8 bg-primary text-primary-foreground rounded-full flex items-center justify-center font-medium">
                {userEmail.charAt(0).toUpperCase()}
              </div>
              <span className="text-muted-foreground hidden sm:inline">
                {userEmail}
              </span>
            </div>
          )}
        </div>
      </header>

      {/* Theme Customizer Modal */}
      <ThemeCustomizer
        isOpen={isThemeCustomizerOpen}
        onClose={() => setIsThemeCustomizerOpen(false)}
      />
    </>
  )
}
