'use client'

import { useState, useRef, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Separator } from '@/components/ui/separator'
import { 
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from '@/components/ui/popover'
import { Note } from '@/lib/storage'
import TextFormattingToolbarFull from '@/components/TextFormattingToolbarFull'

interface EnhancedNoteEditorProps {
  note: Note | null
  onNoteUpdate: (noteId: string, updates: { title?: string; subtitle?: string; content?: string }) => void
}

const fontSizes = ['12px', '14px', '16px', '18px', '20px', '24px', '28px', '32px', '36px']
const fontFamilies = ['Inter', 'Arial', 'Helvetica', 'Times New Roman', 'Georgia', 'Courier New']

const predefinedColors = [
  '#000000', '#333333', '#666666', '#999999', '#CCCCCC', '#FFFFFF',
  '#FF0000', '#FF6600', '#FFCC00', '#00FF00', '#0066FF', '#6600FF',
  '#FF0066', '#FF3366', '#FF6699', '#66FF99', '#6699FF', '#9966FF',
  '#8B4513', '#2F4F4F', '#800080', '#008080', '#FF1493', '#32CD32'
]

export default function EnhancedNoteEditorWithPopup({ note, onNoteUpdate }: EnhancedNoteEditorProps) {
  const [title, setTitle] = useState('')
  const [subtitle, setSubtitle] = useState('')
  const [content, setContent] = useState('')
  const [hasChanges, setHasChanges] = useState(false)
  const [lastSaved, setLastSaved] = useState<Date | null>(null)
  const [isFullscreen, setIsFullscreen] = useState(false)
  
  // Formatting states
  const [currentTextColor, setCurrentTextColor] = useState('#000000')
  const [currentBgColor, setCurrentBgColor] = useState('#ffffff')
  const [currentFontSize, setCurrentFontSize] = useState('16px')
  const [currentFontFamily, setCurrentFontFamily] = useState('Inter')
  
  // Selection popup states
  const [showSelectionPopup, setShowSelectionPopup] = useState(false)
  const [popupPosition, setPopupPosition] = useState({ x: 0, y: 0 })
  const [selectedText, setSelectedText] = useState('')
  
  const contentRef = useRef<HTMLDivElement>(null)

  // Atualizar campos quando a nota selecionada mudar
  useEffect(() => {
    if (note) {
      setTitle(note.title)
      setSubtitle(note.subtitle)
      setContent(note.content)
      setHasChanges(false)
      setLastSaved(new Date(note.updatedAt))
      
      // Atualizar o conteúdo do editor
      if (contentRef.current) {
        contentRef.current.innerHTML = note.content || ''
      }
    } else {
      setTitle('')
      setSubtitle('')
      setContent('')
      setHasChanges(false)
      setLastSaved(null)
      
      if (contentRef.current) {
        contentRef.current.innerHTML = ''
      }
    }
  }, [note])

  // Auto-save após 10 segundos de inatividade
  useEffect(() => {
    if (!note || !hasChanges) return

    const timeoutId = setTimeout(() => {
      handleSave()
    }, 10000)

    return () => clearTimeout(timeoutId)
  }, [title, subtitle, content, hasChanges, note])

  // Handle ESC key to exit fullscreen
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && isFullscreen) {
        setIsFullscreen(false)
      }
    }

    document.addEventListener('keydown', handleKeyDown)
    return () => document.removeEventListener('keydown', handleKeyDown)
  }, [isFullscreen])


  // Handle text selection popup
  useEffect(() => {
    const handleSelectionChange = () => {
      const selection = window.getSelection()
      if (selection && selection.rangeCount > 0) {
        const selectedText = selection.toString().trim()
        
        if (selectedText.length > 0 && contentRef.current?.contains(selection.anchorNode)) {
          const range = selection.getRangeAt(0)
          const rect = range.getBoundingClientRect()
          
          setSelectedText(selectedText)
          // Ajustar posição para não vazar da tela
          const popupWidth = 350
          const popupHeight = 120
          let x = rect.left + rect.width / 2
          let y = rect.top - 10
          
          if (x + popupWidth / 2 > window.innerWidth) {
            x = window.innerWidth - popupWidth / 2 - 10
          }
          if (x - popupWidth / 2 < 0) {
            x = popupWidth / 2 + 10
          }
          if (y - popupHeight < 0) {
            y = rect.bottom + popupHeight + 10
          }
          
          setPopupPosition({
            x,
            y
          })
          setShowSelectionPopup(true)
        } else {
          setShowSelectionPopup(false)
        }
      } else {
        setShowSelectionPopup(false)
      }
    }

    const handleClickOutside = (e: MouseEvent) => {
      if (showSelectionPopup && !(e.target as Element)?.closest('.selection-popup')) {
        setShowSelectionPopup(false)
      }
    }

    document.addEventListener('selectionchange', handleSelectionChange)
    document.addEventListener('mousedown', handleClickOutside)
    
    return () => {
      document.removeEventListener('selectionchange', handleSelectionChange)
      document.removeEventListener('mousedown', handleClickOutside)
    }
  }, [showSelectionPopup])

  const handleSave = () => {
    if (!note || !hasChanges) return

    onNoteUpdate(note.id, {
      title: title.trim() || 'Sem título',
      subtitle: subtitle.trim(),
      content: content.trim()
    })
    
    setHasChanges(false)
    setLastSaved(new Date())
  }

  const handleTitleChange = (value: string) => {
    setTitle(value)
    setHasChanges(true)
  }

  const handleSubtitleChange = (value: string) => {
    setSubtitle(value)
    setHasChanges(true)
  }

  const handleContentChange = () => {
    if (contentRef.current) {
      const newContent = contentRef.current.innerHTML
      setContent(newContent)
      setHasChanges(true)
    }
  }

  const applyFormat = (command: string, value?: string) => {
    document.execCommand(command, false, value)
    handleContentChange()
    
    // Manter o foco no editor
    if (contentRef.current) {
      contentRef.current.focus()
    }
  }

  const handleFontChange = (font: string) => {
    setCurrentFontFamily(font)
    applyFormat('fontName', font)
  }

  const handleFontSizeChange = (size: string) => {
    setCurrentFontSize(size)
    // Aplicar font size via span
    const selection = window.getSelection()
    if (selection && selection.rangeCount > 0) {
      const range = selection.getRangeAt(0)
      if (!range.collapsed) {
        const span = document.createElement('span')
        span.style.fontSize = size
        try {
          range.surroundContents(span)
          handleContentChange()
        } catch {
          applyFormat('fontSize', '4')
        }
      }
    }
  }

  const applyTextColor = (color: string) => {
    const selection = window.getSelection()
    if (selection && selection.rangeCount > 0 && !selection.isCollapsed) {
      document.execCommand('styleWithCSS', false, 'true')
      // Ajustar cor para modo noturno
      const isDark = document.body.classList.contains('dark')
      const adjustedColor = isDark && color === '#000000' ? '#FFFFFF' : color
      document.execCommand('foreColor', false, adjustedColor)
      handleContentChange()
    }
    setShowSelectionPopup(false)
  }

  const applyBackgroundColor = (color: string) => {
    // Usar EyeDropper API para pegar cor da tela se disponível
    if (color === 'eyedropper' && 'EyeDropper' in window) {
      const eyeDropper = new (window as any).EyeDropper()
      eyeDropper.open().then((result: { sRGBHex: string }) => {
        applyBackgroundColor(result.sRGBHex)
      }).catch(() => {
        // Cancelado ou erro, não faz nada
      })
      return
    }

    const selection = window.getSelection()
    if (selection && selection.rangeCount > 0 && !selection.isCollapsed) {
      // Aplicar cor de fundo na caixa de texto (span) ao redor da seleção
      const range = selection.getRangeAt(0)
      const span = document.createElement('span')
      span.style.backgroundColor = color
      span.style.padding = '0 2px'  // Adiciona padding para destacar melhor
      try {
        range.surroundContents(span)
        handleContentChange()
      } catch (e) {
        // Fallback para execCommand
        document.execCommand('styleWithCSS', false, 'true')
        const isDark = document.body.classList.contains('dark')
        const adjustedColor = isDark && color === '#000000' ? '#FFFFFF' : color
        document.execCommand('hiliteColor', false, adjustedColor)
        handleContentChange()
      }
    }
    setShowSelectionPopup(false)
  }

  const applyFontSize = (size: string) => {
    const selection = window.getSelection()
    if (selection && selection.rangeCount > 0 && !selection.isCollapsed) {
      const range = selection.getRangeAt(0)
      const span = document.createElement('span')
      span.style.fontSize = size
      try {
        range.surroundContents(span)
        handleContentChange()
      } catch (e) {
        // Fallback
        applyFormat('fontSize', '4')
      }
    }
    setCurrentFontSize(size)
  }

  const applyFontFamily = (family: string) => {
    setCurrentFontFamily(family)
    applyFormat('fontName', family)
  }

  const formatLastSaved = (date: Date) => {
    const now = new Date()
    const diffInMinutes = (now.getTime() - date.getTime()) / (1000 * 60)

    if (diffInMinutes < 1) {
      return 'Salvo agora'
    } else if (diffInMinutes < 60) {
      return `Salvo há ${Math.floor(diffInMinutes)} min`
    } else {
      return `Salvo às ${date.toLocaleTimeString('pt-BR', { 
        hour: '2-digit', 
        minute: '2-digit' 
      })}`
    }
  }

  const ColorPicker = ({ 
    color, 
    onChange, 
    label 
  }: { 
    color: string
    onChange: (color: string) => void
    label: string 
  }) => (
    <Popover>
      <PopoverTrigger asChild>
        <Button
          variant="outline"
          size="sm"
          className="w-10 h-8 p-0 border-2"
          style={{ backgroundColor: color }}
          title={label}
        >
          <span className="sr-only">{label}</span>
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-72 p-4">
        <div className="space-y-4">
          <div>
            <label className="text-sm font-medium mb-2 block">{label}</label>
            <div className="flex gap-2 mb-3">
              <input
                type="color"
                value={color}
                onChange={(e) => onChange(e.target.value)}
                className="w-16 h-10 border rounded cursor-pointer"
              />
              <input
                type="text"
                value={color}
                onChange={(e) => onChange(e.target.value)}
                className="flex-1 px-3 py-2 text-sm border rounded"
                placeholder="#000000"
              />
            </div>
          </div>
          
          <div>
            <label className="text-sm font-medium mb-2 block">Cores Predefinidas</label>
            <div className="grid grid-cols-6 gap-2">
              {predefinedColors.map((presetColor) => (
                <button
                  key={presetColor}
                  onClick={() => onChange(presetColor)}
                  className="w-10 h-10 border-2 rounded cursor-pointer hover:scale-110 transition-transform"
                  style={{ backgroundColor: presetColor }}
                  title={presetColor}
                />
              ))}
            </div>
          </div>
        </div>
      </PopoverContent>
    </Popover>
  )

  if (!note) {
    return (
      <div className="flex-1 flex items-center justify-center bg-background">
        <div className="text-center space-y-4 max-w-md mx-auto p-8">
          <div className="w-24 h-24 mx-auto bg-muted rounded-full flex items-center justify-center">
            <span className="text-4xl">📝</span>
          </div>
          <div>
            <h3 className="text-lg font-medium text-foreground mb-2">
              Selecione uma anotação
            </h3>
            <p className="text-sm text-muted-foreground">
              Escolha uma anotação da lista ao lado para começar a editar, 
              ou crie uma nova anotação para começar.
            </p>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className={`${isFullscreen ? 'fixed inset-0 z-50 bg-background' : 'flex-1 flex flex-col bg-background'} note-editor`}>
      {/* Header */}
      <div className="border-b border-border p-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="flex items-center gap-2">
              {hasChanges && (
                <div className="w-2 h-2 bg-orange-500 rounded-full animate-pulse"></div>
              )}
              <span className="text-sm text-muted-foreground">
                {hasChanges ? 'Salvando...' : lastSaved ? formatLastSaved(lastSaved) : ''}
              </span>
            </div>
          </div>
          
          <div className="flex items-center gap-2">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setIsFullscreen(!isFullscreen)}
              title={isFullscreen ? 'Sair da tela cheia (ESC)' : 'Tela cheia'}
              className="text-lg"
            >
              {isFullscreen ? '⤓' : '⤢'}
            </Button>
            
          <Button
            variant="outline"
            size="sm"
            onClick={handleSave}
            disabled={!hasChanges}
          >
            Salvar agora
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={() => {
              if (!contentRef.current) return
              const element = document.createElement('a')
              const file = new Blob([contentRef.current.innerHTML], { type: 'text/html' })
              element.href = URL.createObjectURL(file)
              element.download = `${title || 'nota'}.html`
              document.body.appendChild(element)
              element.click()
              document.body.removeChild(element)
            }}
            className="ml-2"
          >
            Exportar
          </Button>
          </div>
        </div>
      </div>

      {/* Title and Subtitle - Only show if not fullscreen */}
      {!isFullscreen && (
        <div className="p-6 border-b border-border space-y-4">
          <div>
            <Input
              placeholder="Título da anotação"
              value={title}
              onChange={(e) => handleTitleChange(e.target.value)}
              className="text-2xl font-bold border-none px-0 py-2 h-auto bg-transparent focus-visible:ring-0 focus-visible:ring-offset-0"
              style={{ fontSize: '1.5rem', lineHeight: '2rem' }}
            />
          </div>

          <div>
            <Input
              placeholder="Subtítulo (opcional)"
              value={subtitle}
              onChange={(e) => handleSubtitleChange(e.target.value)}
              className="text-lg text-muted-foreground border-none px-0 py-1 h-auto bg-transparent focus-visible:ring-0 focus-visible:ring-offset-0"
            />
          </div>
        </div>
      )}

      {/* Formatting Toolbar */}
      <div className="flex items-center gap-2 p-3 border-b border-border bg-card overflow-x-auto">
        {/* Font Family */}
        <Select value={currentFontFamily} onValueChange={applyFontFamily}>
          <SelectTrigger className="w-36 h-9">
            <SelectValue />
          </SelectTrigger>
          <SelectContent>
            {fontFamilies.map((font) => (
              <SelectItem key={font} value={font}>
                <span style={{ fontFamily: font }}>{font}</span>
              </SelectItem>
            ))}
          </SelectContent>
        </Select>

        <Separator orientation="vertical" className="h-6 mx-1" />

        {/* Font Size */}
        <Select value={currentFontSize} onValueChange={applyFontSize}>
          <SelectTrigger className="w-20 h-9">
            <SelectValue />
          </SelectTrigger>
          <SelectContent>
            {fontSizes.map((size) => (
              <SelectItem key={size} value={size}>
                {size}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>

        <Separator orientation="vertical" className="h-6 mx-1" />

        {/* Text Formatting */}
        <Button
          variant="ghost"
          size="sm"
          className="w-9 h-9 font-bold"
          onClick={() => applyFormat('bold')}
          title="Negrito"
        >
          B
        </Button>

        <Button
          variant="ghost"
          size="sm"
          className="w-9 h-9 italic"
          onClick={() => applyFormat('italic')}
          title="Itálico"
        >
          I
        </Button>

        <Button
          variant="ghost"
          size="sm"
          className="w-9 h-9 underline"
          onClick={() => applyFormat('underline')}
          title="Sublinhado"
        >
          U
        </Button>

        <Separator orientation="vertical" className="h-6 mx-1" />

        {/* Colors */}
        <ColorPicker
          color={currentTextColor}
          onChange={applyTextColor}
          label="Cor do Texto"
        />

        <ColorPicker
          color={currentBgColor}
          onChange={applyBackgroundColor}
          label="Cor de Fundo"
        />

        <Separator orientation="vertical" className="h-6 mx-1" />

        {/* Lists */}
        <Button
          variant="ghost"
          size="sm"
          className="w-9 h-9"
          onClick={() => applyFormat('insertUnorderedList')}
          title="Lista com marcadores"
        >
          •
        </Button>

        <Button
          variant="ghost"
          size="sm"
          className="w-9 h-9"
          onClick={() => applyFormat('insertOrderedList')}
          title="Lista numerada"
        >
          1.
        </Button>

        <Separator orientation="vertical" className="h-6 mx-1" />

        {/* Alignment */}
        <Button
          variant="ghost"
          size="sm"
          className="w-9 h-9"
          onClick={() => applyFormat('justifyLeft')}
          title="Alinhar à esquerda"
        >
          ⫷
        </Button>

        <Button
          variant="ghost"
          size="sm"
          className="w-9 h-9"
          onClick={() => applyFormat('justifyCenter')}
          title="Centralizar"
        >
          ⫸
        </Button>

        <Button
          variant="ghost"
          size="sm"
          className="w-9 h-9"
          onClick={() => applyFormat('justifyRight')}
          title="Alinhar à direita"
        >
          ⫹
        </Button>
      </div>

      {/* Rich Text Content */}
      <div className="flex-1 overflow-auto relative">
        <div
          ref={contentRef}
          contentEditable
          suppressContentEditableWarning={true}
          onInput={handleContentChange}
          onBlur={handleContentChange}
          className={`
            ${isFullscreen ? 'h-screen min-h-screen p-12' : 'min-h-[500px] p-8'} 
            w-full outline-none text-base leading-relaxed bg-background
          `}
          style={{ 
            fontSize: currentFontSize,
            fontFamily: currentFontFamily,
            lineHeight: '1.8',
            color: currentTextColor,
            backgroundColor: currentBgColor
          }}
          data-placeholder={isFullscreen ? 
            "Modo tela cheia ativado. Foque na sua escrita... (Pressione ESC para sair)" : 
            "Comece a escrever sua anotação aqui... Selecione texto para ver opções de formatação."
          }
        />

        {/* Selection Popup */}
        {showSelectionPopup && (
        <div
          className="selection-popup fixed z-[60] bg-white border-2 border-gray-300 rounded-xl shadow-xl p-4 bottom-4 right-4 max-w-[320px]"
          style={{
            transform: 'translate(0, 0)'
          }}
        >
          <div className="text-center mb-3">
            <span className="text-sm font-medium text-gray-700">
              Formatação do Texto Selecionado
            </span>
          </div>
          
          {/* Text Colors */}
          <div className="mb-4">
            <div className="text-xs font-medium text-gray-600 mb-2 text-center">COR DO TEXTO</div>
            <div className="flex justify-center gap-2">
              {['#000000', '#FFFFFF', '#FF0000', '#00AA00', '#0066FF', '#FF6600', '#AA00AA'].map((color) => (
                <button
                  key={`text-${color}`}
                  onClick={() => applyTextColor(color)}
                  className="w-8 h-8 rounded-full border-3 border-gray-400 shadow-md hover:scale-125 transition-all duration-200 hover:shadow-lg"
                  style={{ 
                    backgroundColor: color,
                    border: color === '#FFFFFF' ? '3px solid #ccc' : '3px solid #666'
                  }}
                  title={`Cor do texto: ${color}`}
                />
              ))}
            </div>
          </div>

          {/* Background Colors */}
          <div className="mb-4">
            <div className="text-xs font-medium text-gray-600 mb-2 text-center">DESTAQUE (FUNDO)</div>
            <div className="flex justify-center gap-2">
              {['#FFFF00', '#00FFFF', '#FF69B4', '#90EE90', '#FFB6C1', '#DDA0DD', '#F0E68C'].map((color) => (
                <button
                  key={`bg-${color}`}
                  onClick={() => applyBackgroundColor(color)}
                  className="w-8 h-8 rounded-full border-3 border-gray-400 shadow-md hover:scale-125 transition-all duration-200 hover:shadow-lg"
                  style={{ backgroundColor: color }}
                  title={`Destacar com: ${color}`}
                />
              ))}
            </div>
          </div>

          {/* Remove formatting */}
          <div className="text-center">
            <button
              onClick={() => {
                const selection = window.getSelection()
                if (selection && selection.rangeCount > 0 && !selection.isCollapsed) {
                  document.execCommand('removeFormat', false, '')
                  handleContentChange()
                }
                setShowSelectionPopup(false)
              }}
              className="text-xs text-gray-500 hover:text-gray-700 underline"
            >
              Remover formatação
            </button>
          </div>
        </div>
        )}
      </div>

      {/* Metadata - Only show if not fullscreen */}
      {!isFullscreen && (
        <div className="p-6 border-t border-border">
          <div className="flex items-center justify-between text-xs text-muted-foreground">
            <div>
              Criado em {new Date(note.createdAt).toLocaleDateString('pt-BR', {
                day: '2-digit',
                month: '2-digit',
                year: 'numeric',
                hour: '2-digit',
                minute: '2-digit'
              })}
            </div>
            <div>
              {content.replace(/<[^>]*>/g, '').length} caracteres
            </div>
          </div>
        </div>
      )}

      <style jsx>{`
        [contenteditable]:empty:before {
          content: attr(data-placeholder);
          color: #9ca3af;
          pointer-events: none;
          font-style: italic;
        }
        
        [contenteditable] {
          word-wrap: break-word;
          overflow-wrap: break-word;
        }
        
        [contenteditable] * {
          max-width: 100%;
        }

        .selection-popup {
          animation: fadeIn 0.2s ease-out;
        }

        @keyframes fadeIn {
          from {
            opacity: 0;
            transform: translate(-50%, -100%) scale(0.95);
          }
          to {
            opacity: 1;
            transform: translate(-50%, -100%) scale(1);
          }
        }
      `}</style>
    </div>
  )
}
