'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { ScrollArea } from '@/components/ui/scroll-area'
import { Separator } from '@/components/ui/separator'
import { 
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from '@/components/ui/alert-dialog'
import { Folder } from '@/lib/storage'

interface SidebarProps {
  folders: Folder[]
  selectedFolderId: string | null
  onFolderSelect: (folderId: string) => void
  onFolderAdd: (name: string) => void
  onFolderUpdate: (folderId: string, name: string) => void
  onFolderDelete: (folderId: string) => void
  onLogout: () => void
}

export default function Sidebar({
  folders,
  selectedFolderId,
  onFolderSelect,
  onFolderAdd,
  onFolderUpdate,
  onFolderDelete,
  onLogout
}: SidebarProps) {
  const [isAddingFolder, setIsAddingFolder] = useState(false)
  const [newFolderName, setNewFolderName] = useState('')
  const [editingFolderId, setEditingFolderId] = useState<string | null>(null)
  const [editingName, setEditingName] = useState('')

  const handleAddFolder = () => {
    if (newFolderName.trim()) {
      onFolderAdd(newFolderName.trim())
      setNewFolderName('')
      setIsAddingFolder(false)
    }
  }

  const handleEditFolder = (folder: Folder) => {
    setEditingFolderId(folder.id)
    setEditingName(folder.name)
  }

  const handleSaveEdit = () => {
    if (editingFolderId && editingName.trim()) {
      onFolderUpdate(editingFolderId, editingName.trim())
      setEditingFolderId(null)
      setEditingName('')
    }
  }

  const handleCancelEdit = () => {
    setEditingFolderId(null)
    setEditingName('')
  }

  return (
    <div className="w-64 bg-card border-r border-border h-full flex flex-col">
      {/* Header */}
      <div className="p-4 border-b border-border">
        <h2 className="text-lg font-semibold text-foreground">NotesApp</h2>
        <p className="text-sm text-muted-foreground">Suas anotações organizadas</p>
      </div>

      {/* Folders */}
      <div className="flex-1 flex flex-col">
        <div className="p-4">
          <div className="flex items-center justify-between mb-3">
            <h3 className="text-sm font-medium text-foreground">Pastas</h3>
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setIsAddingFolder(true)}
              className="h-6 w-6 p-0"
            >
              +
            </Button>
          </div>

          {/* Add new folder input */}
          {isAddingFolder && (
            <div className="mb-3 space-y-2">
              <Input
                placeholder="Nome da pasta"
                value={newFolderName}
                onChange={(e) => setNewFolderName(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') handleAddFolder()
                  if (e.key === 'Escape') {
                    setIsAddingFolder(false)
                    setNewFolderName('')
                  }
                }}
                autoFocus
                className="h-8"
              />
              <div className="flex gap-1">
                <Button size="sm" onClick={handleAddFolder} className="h-6 text-xs">
                  Salvar
                </Button>
                <Button 
                  size="sm" 
                  variant="ghost" 
                  onClick={() => {
                    setIsAddingFolder(false)
                    setNewFolderName('')
                  }}
                  className="h-6 text-xs"
                >
                  Cancelar
                </Button>
              </div>
            </div>
          )}
        </div>

        {/* Folders list */}
        <ScrollArea className="flex-1 px-4">
          <div className="space-y-1">
            {folders.map((folder) => (
              <div key={folder.id} className="group">
                {editingFolderId === folder.id ? (
                  <div className="space-y-2">
                    <Input
                      value={editingName}
                      onChange={(e) => setEditingName(e.target.value)}
                      onKeyDown={(e) => {
                        if (e.key === 'Enter') handleSaveEdit()
                        if (e.key === 'Escape') handleCancelEdit()
                      }}
                      className="h-8"
                      autoFocus
                    />
                    <div className="flex gap-1">
                      <Button size="sm" onClick={handleSaveEdit} className="h-6 text-xs">
                        Salvar
                      </Button>
                      <Button 
                        size="sm" 
                        variant="ghost" 
                        onClick={handleCancelEdit}
                        className="h-6 text-xs"
                      >
                        Cancelar
                      </Button>
                    </div>
                  </div>
                ) : (
                  <div className="flex items-center group">
                    <Button
                      variant={selectedFolderId === folder.id ? "secondary" : "ghost"}
                      className="flex-1 justify-start h-8 px-2 text-sm"
                      onClick={() => onFolderSelect(folder.id)}
                    >
                      <span className="truncate">{folder.name}</span>
                    </Button>
                    
                    <div className="opacity-0 group-hover:opacity-100 flex gap-1 ml-1">
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={() => handleEditFolder(folder)}
                        className="h-6 w-6 p-0 text-xs"
                      >
                        ✏️
                      </Button>
                      
                      <AlertDialog>
                        <AlertDialogTrigger asChild>
                          <Button
                            variant="ghost"
                            size="sm"
                            className="h-6 w-6 p-0 text-xs text-destructive hover:text-destructive"
                          >
                            🗑️
                          </Button>
                        </AlertDialogTrigger>
                        <AlertDialogContent>
                          <AlertDialogHeader>
                            <AlertDialogTitle>Excluir pasta</AlertDialogTitle>
                            <AlertDialogDescription>
                              Tem certeza que deseja excluir a pasta "{folder.name}"? 
                              Todas as anotações desta pasta também serão excluídas. 
                              Esta ação não pode ser desfeita.
                            </AlertDialogDescription>
                          </AlertDialogHeader>
                          <AlertDialogFooter>
                            <AlertDialogCancel>Cancelar</AlertDialogCancel>
                            <AlertDialogAction
                              onClick={() => onFolderDelete(folder.id)}
                              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
                            >
                              Excluir
                            </AlertDialogAction>
                          </AlertDialogFooter>
                        </AlertDialogContent>
                      </AlertDialog>
                    </div>
                  </div>
                )}
              </div>
            ))}
          </div>
        </ScrollArea>
      </div>

      {/* Footer */}
      <div className="p-4 border-t border-border">
        <Separator className="mb-3" />
        <Button
          variant="ghost"
          size="sm"
          onClick={onLogout}
          className="w-full justify-start text-sm text-muted-foreground hover:text-foreground"
        >
          Sair
        </Button>
      </div>
    </div>
  )
}
