'use client'

import { useState, useEffect } from 'react'
import { NotesData, loadData, saveData } from '@/lib/storage'

export const useLocalStorage = () => {
  const [data, setData] = useState<NotesData>({ folders: [], notes: [] })
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    try {
      const loadedData = loadData()
      setData(loadedData)
    } catch (error) {
      console.error('Erro ao carregar dados do localStorage:', error)
    } finally {
      setIsLoading(false)
    }
  }, [])

  const updateData = (newData: NotesData) => {
    try {
      setData(newData)
      saveData(newData)
    } catch (error) {
      console.error('Erro ao salvar dados:', error)
      throw error
    }
  }

  const addFolder = (name: string) => {
    const newFolder = {
      id: Date.now().toString(36) + Math.random().toString(36).substr(2),
      name,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    }

    const newData = {
      ...data,
      folders: [...data.folders, newFolder]
    }
    updateData(newData)
    return newFolder
  }

  const updateFolder = (folderId: string, name: string) => {
    const newData = {
      ...data,
      folders: data.folders.map(folder =>
        folder.id === folderId
          ? { ...folder, name, updatedAt: new Date().toISOString() }
          : folder
      )
    }
    updateData(newData)
  }

  const deleteFolder = (folderId: string) => {
    const newData = {
      folders: data.folders.filter(folder => folder.id !== folderId),
      notes: data.notes.filter(note => note.folderId !== folderId)
    }
    updateData(newData)
  }

  const addNote = (folderId: string, title: string = 'Nova Anotação') => {
    const newNote = {
      id: Date.now().toString(36) + Math.random().toString(36).substr(2),
      title,
      subtitle: '',
      content: '',
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
      folderId
    }

    const newData = {
      ...data,
      notes: [...data.notes, newNote]
    }
    updateData(newData)
    return newNote
  }

  const updateNote = (noteId: string, updates: { title?: string; subtitle?: string; content?: string }) => {
    const newData = {
      ...data,
      notes: data.notes.map(note =>
        note.id === noteId
          ? { ...note, ...updates, updatedAt: new Date().toISOString() }
          : note
      )
    }
    updateData(newData)
  }

  const deleteNote = (noteId: string) => {
    const newData = {
      ...data,
      notes: data.notes.filter(note => note.id !== noteId)
    }
    updateData(newData)
  }

  const searchNotes = (query: string) => {
    if (!query.trim()) return data.notes

    const lowercaseQuery = query.toLowerCase()
    return data.notes.filter(note =>
      note.title.toLowerCase().includes(lowercaseQuery) ||
      note.subtitle.toLowerCase().includes(lowercaseQuery) ||
      note.content.toLowerCase().includes(lowercaseQuery)
    )
  }

  return {
    data,
    isLoading,
    addFolder,
    updateFolder,
    deleteFolder,
    addNote,
    updateNote,
    deleteNote,
    searchNotes
  }
}
